<template lang="pug">
  .profile.inner-page(v-if="getUsersInfo")
    .inner-page__main
      .profile__info
        profile-info(:info="getUsersInfo" :blocked="getUsersInfo.is_blocked" :friend="getUsersInfo.is_friend" :online="getUsersInfo.is_onlined")
      .profile__news
        .profile__tabs
          span.profile__tab.active Публикации {{getUsersInfo.first_name}} ({{getAllActivePosts}})
        .profile__news-list
          news-block(v-for="news in getWall" :key="news.id" :info="news")
          button.btn(@click="loadPosts" v-if="loadPostsBtn") ещё
    .inner-page__aside
      friends-request
      br
      friends-possible
</template>

<script>
import FriendsPossible from '@/components/Friends/Possible'
import FriendsRequest from '@/components/Friends/Request'
import ProfileInfo from '@/components/Profile/Info'
import NewsBlock from '@/components/News/Block'
import { mapActions, mapGetters } from 'vuex'
export default {
  name: 'ProfileId',
  components: { FriendsPossible, FriendsRequest, ProfileInfo, NewsBlock },
  data: () => ({
    loading: false,
    loadPostsBtn: true,
    offset: 0,
    itemPerPage: 20,
  }),
  computed: {
    ...mapGetters('users/info', ['getUsersInfo', 'getWall', 'getAllActivePosts'])
  },
  methods: {
    ...mapActions('users/info', ['userInfoId']),
    loadPosts() {
      if (this.offset <= this.getAllActivePosts) {
        this.offset += this.itemPerPage
        this.userInfoId({id: this.$route.params.id, offset: this.offset});
      } else {
        this.loadPostsBtn = false
      }
    }
  },
  created() {
    this.$store.commit('users/info/setResetWall', []);

    this.userInfoId({id: this.$route.params.id, offset: this.offset});

  }
}

</script>



